import React from 'react'
import Wrapper from '../subcomponents/wrapper/Wrapper'

import './Header.css'

const Header = (props) => {
  return (
    <header className='header'>
      <Wrapper>
        <div className='header__container'>
          <h1 className='header__title'>Доставим любую корреспонденцию <span>быстро</span></h1>
          <div className='header__sub-title'>Получение писем без очередей и ожидания</div>
          <button className='header__btn'>Перейти в личный кабинет</button>
        </div>
      </Wrapper>
    </header>
  )
}

export default Header;