import React from 'react'
import Slider from 'react-slick'
import Wrapper from '../subcomponents/wrapper/Wrapper'
import { ReactComponent as Item1 } from '../../svg/advantages/item1.svg'
import { ReactComponent as Item2 } from '../../svg/advantages/item2.svg'
import { ReactComponent as Item3 } from '../../svg/advantages/item3.svg'
import { ReactComponent as Item4 } from '../../svg/advantages/item4.svg'
import { ReactComponent as Item5 } from '../../svg/advantages/item5.svg'
import { ReactComponent as Item6 } from '../../svg/advantages/item6.svg'

import './Advantages.css'

const Advantages = (props) => {
  const settings = {
    dots: true,
    arrows: false,
    draggable: true,
    infinite: false,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 2,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 440,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      }
    ]
  };
  return (
    <div className='advantages'>
      <Wrapper>
        <div className="advantages__title">Наши преимущества</div>
        <div className="advantages__container">
          <div className="advantages__item">
            <div className="advantages__circle"><Item1 /></div>
            <div className="advantages__item-title">Уникальный механизм доставки</div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item2 /></div>
            <div className="advantages__item-title">Лицензированный оператор услуг почтовой связи</div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item3 /></div>
            <div className="advantages__item-title">Сами забираем корреспонденцию</div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item4 /></div>
            <div className="advantages__item-title">Без очередей и ожидания</div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item5 /></div>
            <div className="advantages__item-title">Получение без паспорта по смс</div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item6 /></div>
            <div className="advantages__item-title">Возможность электронного уведомления</div>
          </div>
        </div>
        <div className='advantages__container-carousel'>
          <Slider {...settings}>
            <div className="advantages__item">
              <div className="advantages__circle"><Item1 /></div>
              <div className="advantages__item-title">Уникальный механизм доставки</div>
            </div>
            <div className="advantages__item">
              <div className="advantages__circle"><Item2 /></div>
              <div className="advantages__item-title">Лицензированный оператор услуг почтовой связи</div>
            </div>
            <div className="advantages__item">
              <div className="advantages__circle"><Item3 /></div>
              <div className="advantages__item-title">Сами забираем корреспонденцию</div>
            </div>
            <div className="advantages__item">
              <div className="advantages__circle"><Item4 /></div>
              <div className="advantages__item-title">Без очередей и ожидания</div>
            </div>
            <div className="advantages__item">
              <div className="advantages__circle"><Item5 /></div>
              <div className="advantages__item-title">Получение без паспорта по смс</div>
            </div>
            <div className="advantages__item">
              <div className="advantages__circle"><Item6 /></div>
              <div className="advantages__item-title">Возможность электронного уведомления</div>
            </div>
          </Slider>
        </div>
      </Wrapper>
    </div>
  )
}

export default Advantages;
